import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Accordion from 'react-bootstrap/Accordion';


import axios from 'axios';

class Classcomponent extends Component {
    constructor(props){
        super(props)
        this.state ={
            message : '  ...',
            interviewData : [],
            deleteDB : async (key_id) => {
                try{
               
                    axios.post('/api?type=interviewDelete', {
                        
                        body :{ 
                                no : key_id,
                                crud : 'delete',
                                mapper : this.props.dbinfo.mapper,
                                mapperid :'interviewDelete'
                         }
            
                    }).then( res => {                 
                        console.log(res.data) //              
                        try{                  
                            this.setState({ message : '. '});
                        }
                        catch(err){
                            this.setState({ message : 'DB ' +  err});
                        }
            
                    }).catch( err => {
                        this.setState({ message : '   ' +  err});
                    })
                   }
                   catch(err){
                       this.setState({ message : ' ' +  err});
                   }  
        
            }
            
        }
        
    }
    componentDidMount = async () => {
        try{
       
        axios.post('/api?type='+this.props.dbinfo.botable, {
            headers :{ 'Content-Type' : 'application/json' },
            body : { ...this.props.dbinfo }

        }).then( res => { 
            
            console.log(res.data) //  
        
            try{
                this.setState({ interviewData : [...res.data] }) 
                // array    
                //  []  res.data  
                console.log(this.state.interviewData) //     map 
            }
            catch(err){
                this.setState({ message : 'DB ' +  err});
            }
        }).catch( err => {
            this.setState({ message : '   ' +  err});
        })
       }
       catch(err){
           this.setState({ message : ' ' +  err});
       } 
    }

    render() {
        return (
            <Accordion defaultActiveKey="0" flush className={ ' container  py-5'}  tag ='div' >
                <h2 className='faq'>{ this.state.interviewData.length > 0 ? this.props.dbinfo.titlenm + "("+ this.state.interviewData.length + ")" : this.state.message } </h2>
                    {
                     this.state.interviewData.map(
                         (content,i) =>
                         {
                             return (
                                <Accordion.Item eventKey={i.toString()} key={ content.key_id } >
                                    <Accordion.Header>
                                        <strong className='row justify-content-between align-items-center w-100'>
                                            <span className='faqtext col-sm-9 mb-0'> { content.cyh_subject }</span>
                                            <span className='faqtext btn interviewBtn col-sm-3  mb-0'>
                                                {/* <Link to={'/interviewModify/'+ content.key_id } className='modify btn btn-primary mx-1 '>M</Link> 
                                                <a href="#" onClick={ e=>{ this.state.deleteDB(content.key_id) }}  className='delete btn btn-primary mx-1'>D</a> */}
                                            </span>                                            
                                        </strong>
                                    </Accordion.Header>
                                    <Accordion.Body>
                                            { content.cyh_content }
                                    </Accordion.Body>
                                </Accordion.Item>
                             )
                         }
                     )
                    }
            </Accordion>            
        );
    }
}

export default Classcomponent;